import React, { useEffect } from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/Layout';
import Seo from '../components/Seo';
import Hero from '../components/Hero';
import Projects from '../components/Projects'
import FormDemo from '../components/FormDemo';
import ImageDemo from '../components/ImageDemo';
import { useAccessWidget } from '../hooks';

const Index = ({ data }) => {
  useAccessWidget();

  return (
    <Layout>
      <Seo />
      <Hero
        title={data.site.siteMetadata.title}
        subtitle={data.site.siteMetadata.jobTitle}
      />
      <p>This is a page for testing and demoing some tools. Right now we are testing out AccessiBe. <a href="https://accessibe.com">Click here</a> for AccessiBe's website.</p>
      <FormDemo />
      <ImageDemo />
      <Projects />
    </Layout>
  )
};

export default Index;

export const query = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
        jobTitle
      }
    }
  }
`;
